body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {

  white-space: nowrap;
  -webkit-writing-mode: vertical-lr;
      -ms-writing-mode: tb-lr;
          writing-mode: vertical-lr;

  overflow-y: hidden;

}

.word-wrapper {

  white-space: normal;

  white-space: initial;

  padding: 8px;

  height: calc(100%-8px);
  
  word-break: break-all;

}

.character-wrapper {

  display: inline-block;

  padding: 0;
  margin: 3px;

  width: 14px;
  height: 12px;

  line-height: 0;

}

.character-cell {

  --line: 2px; 

  display: inline-block;

  padding: 0;
  margin: 0;

  width: calc(50% - 1px*2);

  width: calc(50% - var(--line, 1px)*2);
  height: calc(50% - 1px*2);
  height: calc(50% - var(--line, 1px)*2);

  border: 1px solid white;

  border: var(--line, 1px) solid white;

}

.character-cell.top {

  border-top-color: black;

}

.character-cell.bottom {

  border-bottom-color: black;

}

.character-cell.left {

  border-left-color: black;

}

.character-cell.right {

  border-right-color: black;

}

.black.word-wrapper {

  background: black

}

.black .character-cell {

  border-color:black;

}

.black .character-cell.top {

  border-top-color: white;

}

.black .character-cell.bottom {

  border-bottom-color: white;

}

.black .character-cell.left {

  border-left-color: white;

}

.black .character-cell.right {

  border-right-color: white;

}
